/*global fetch*/
import React, { Component } from 'react';
import { Line } from 'rc-progress';
import 'bootstrap/dist/css/bootstrap.css';
import 'font-awesome/css/font-awesome.css'
import empty from './assets/empty.png'
import chroma from 'chroma-js'

class Predictions extends Component {
    static defaultProps = {
        originalImage: empty,
        predictions: []
    }
    render() {
        const {originalImage, predictions} = this.props
        let predictionList = predictions.map((pred,index) => {
            const category = pred[0]
            const prob = Math.round(pred[1]*100)
            const scale = chroma.scale(['lightyellow', 'navy']).domain([1, 100], 7, 'log');
            const color = scale(prob).hex()
            console.log(color)
            return (
                <div key={index} className="row my-2 align-items-center" style={{height: "25px"}}>
                    <Line className="col-7" percent={prob} strokeWidth="4" strokeColor={color} trailColor="#D3D3D3" />
                    <p className="col-5" style={{fontWeight: "bold"}}>{category}: {prob}%</p>
                </div>
              )
        })
        return (
            <div className="d-flex justify-content-center mt-5">
                <img src={originalImage} alt="preview" width="200" height="200"/>
                <div className="ml-3">
                    {predictionList}
                </div>
            </div>
            
            )
        
    }
}

//<img src={this.state.uploadedUrl} alt="preview" width="200" height="200"/>

export default Predictions;



