import React from 'react'
import loadingGif from './assets/loading.gif'
import loadingGifWhite from './assets/spinner-white.gif'
import loadingGifDark from './assets/spinner-dark.gif'

export default function LoadingGif(props) {
    let gif = loadingGif
    if (props.theme) {
        switch (props.theme) {
            case "white":
                gif = loadingGifWhite
                break
            case "dark":
                gif = loadingGifDark
                break
            default:
                gif = loadingGif 
        }      
    }
    return (
        <div style={{textAlign: 'center'}}>
            <img 
                src={gif} 
                alt="Loading..." 
                style={{alignSelf: 'center'}}
                height="50%"/>
        </div>    
    )
}


