/*global fetch*/
import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import 'font-awesome/css/font-awesome.css'
import './App.css';
import LoadingGif from './LoadingGif'
import Predictions from './Predictions'

class App extends Component {
  constructor(props) {
        super(props);
         this.state = { 
             picture: null ,
             uploadedUrl: null,
             predictions: null,
             isLoading: false
         };
    }
    uploadHandler = ()=> {
        if (this.state.picture) {
            this.setState({isLoading: true}, function() {
                const formData = new FormData()
                //console.log(this.state.picture)
                formData.append('sampleImage', this.state.picture, this.state.picture.name)
                fetch('image-upload', {
                  method: 'PUT',
                  body: formData
                })
                .then(response => response.json())
                .catch(error => console.error('Error:', error))
                .then(response => {
                    console.log('Success:', response)
                    //this.setState({uploadedUrl: response.file})
                    this.setState({
                        prediction: response.predictions,
                        originalImage: response.originalImage,
                        isLoading: false
                    })
                });
            })
        }
        else {
            console.log('no image')
        }
    }
    fileChangedHandler = (event)=> {
        this.setState({picture: event.target.files[0]})
    }
 
    render() {
        let resultSection  = null
        if (this.state.isLoading) {
            resultSection = <LoadingGif/>
        }
        else if (this.state.prediction) {
            resultSection = (
                <Predictions 
                    predictions={this.state.prediction} 
                    originalImage={this.state.originalImage}
                />)
        }
        return (
          <div className="main-page">
            <nav className="mb-3 p-3">
                <h3 className="text-center">Wide residual network testing</h3>
            </nav>
            <main className="container">
                <div className="form-group col-md-6 offset-md-3  d-flex align-items-center">
                    <div className="d-flex align-items-center my-4">
                        <i className="fa fa-image mr-2 font-12 img-label"></i>
                        <input className="form-control" type="file" onChange={this.fileChangedHandler}/>
                    </div>
                    <button className="ml-3 btn btn-success" onClick={this.uploadHandler}>Recognize!</button>
                </div>
                <section>
                    {resultSection}
                </section>
            </main>
          </div>  
        );
    }
}

export default App;



